* {
    box-sizing: border-box;
}


html {
    font-family: "Quicksand-Light";
    line-height: 1.6;
}

body {
    font-size: rem(18px);
    margin: 0;
    max-width: 1024px;
    margin: auto;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0 0 0.6em;
    line-height: 1;
}



img {
    width: 100%;
}

p {
    margin: 0 0 1em;
}

a {
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
}

